<template>
  <div class="pr">
    <div class="swiper-container-main">
      <div class="swiper-wrapper">
        <div class="text-img-container">
          <img src="@/assets/main-text-ic.png" />
        </div>
        <div class="text-footer-container">
          <img src="@/assets/main-footer-ic.png" />
        </div>
      </div>
    </div>
    <div class="upDirection upDirectionAnimation">&nbsp;</div>
  </div>
</template>

<script>
import Swiper from 'swiper'
import '@/package/swiper-bundle.min.css'
export default {
  name: "poster",
  data() {
    return {};
  },
  methods: {
  },
  mounted() {
    const me = this;
    const mySwiper = new Swiper ('.swiper-container-main', {
      direction : 'vertical',
      on: {
        slideChange: function(swiper) {
          me.$router.push({
            path: 'chooseWishModel'
          })
        }
      }
    })
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.upDirection{
  width: 4px;
  height: 15px;
  position: absolute;
  bottom: 50px;
  left: 50%;
  margin-left: -2px;
  background-color: #fff;
  z-index: 1;
}
.upDirection:after{
  content:' ';
  position: absolute;
  right: -6px;
  top: -10px;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 10px solid #fff;
}
.upDirectionAnimation{
  -webkit-animation:bounce 1.5s .2s infinite ease both;
  -moz-animation:bounce 1.5s .2s infinite ease both;
}
@-webkit-keyframes bounce{
  0%,20%,50%,80%,100%{-webkit-transform:translateY(0)}
  40%{-webkit-transform:translateY(-30px)}
  60%{-webkit-transform:translateY(-15px)}
}
@-moz-keyframes bounce{
  0%,20%,50%,80%,100%{-moz-transform:translateY(0)}
  40%{-moz-transform:translateY(-30px)}
  60%{-moz-transform:translateY(-15px)}
}
.swiper-container-main {
  position: relative;
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
	display: flex;
	flex-direction: column;
	align-items: center;
  justify-content: center;
  background: url("../assets/main-bg.png") no-repeat;  
  background-size: cover;
  background-size:100% 100vh;
}
.text-img-container {
  position: fixed;
  top: 41px;
  width: 265px;
  animation:myfirst 1s;
  animation-fill-mode: forwards
}
.text-footer-container {
  position: fixed;
  bottom: 31px;
  width: 297px;
}
.wishBox{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.swiper-wrapper {
  margin-top: 0;
}
.img-container {
  margin: 30px 0px;
}

@keyframes myfirst
{
	0%   {margin-left:0px;}
	100% {margin-left:25px;}
}

</style>



