<template>
  <div class="chooseWishModel">
			<div class="modelTitle"><img src="@/assets/choose-wish-title.png" /></div>
      <div class="modelItem"  @click="next(index)" v-for="(modelItem,index) in introduceConfig" :key="index">
        <img :src="modelItem.image" class="modelBanner"/>
      </div>
      <div class="jumpTitle"><img src="@/assets/jump-title.png" /></div>
  </div>
</template>

<script>
import {introduceConfig} from '@/constance/introduceConfig'
export default {
  name: "chooseWishModel",
  data() {
    return {
      introduceConfig,
      currentPage: introduceConfig[0]
    };
  },
  mounted(){
    
  },
  methods: {
    next(index) {
      localStorage.setItem('addressIndex',index);
      this.$router.push({
        path: 'chooseWishPrePage',
        query: {
          index
        }
      })
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.chooseWishModel{
    width: 100%;
    padding: 30px 20px 30px;
    background: url("../assets/choose-wish-bg.png") no-repeat;
    background-size: cover;
    height: 100vh;
    background-size:100% 100vh;
}
.modelTitle{
  width: 260px;    
  margin: 0 auto;
	padding-top: 20px;
}
.modelItem{
    height: 113px;
    margin-top: 8px;
    overflow: hidden;
    display: inline-block;
    box-sizing: border-box;
    padding: 0 14px;
}
.modelBanner{
	width: 113px;
}
.modelButton{
	width: 100px;
	height: 35px;
	font-size: 14px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	box-sizing: border-box;
	border: 1px solid #e9e9e9;
	position: fixed;
	left: calc(50% - 50px);
	bottom: 50px;
}
.jumpTitle {
  width: 260px;
  height: 30px;
  margin: 0 auto;
  margin-top: 3px;
}
</style>



