<template>
  <div class="poster" v-show="indexShow">
    <div class="wishBox">
      <img :src="posterBg" class="posterBg"/>
      <img src="@/assets/poster/posterIcon.png" class="posterIcon"/>
      <div class="posterMessageBox">
        <img :src="katongSrc" class="personModel">
        <img :src="posterMessageSrc" class="posterMessage">
        <div class="numBox">
          <div>
            <span>我是第</span>
            <span class="num">10000个</span>
          </div>
          <div>为党送祝福的人</div>
        </div>
      </div>
    </div>
    <div class="tips">小tips：长按可以保存图片</div>
  </div>

  
</template>

<script>
import html2canvas from "html2canvas";
let c = null;
export default {
  name: "poster",
  data() {
    return {
      indexShow:true,
      posterBg:'',
      posterMessageSrc:'',
      katongSrc:'',
      source:''
    };
  },
  mounted(){
    let _this = this;
    let posterBgIndex = Number(localStorage.getItem('addressIndex')) + 1;
    console.log('@/assets/poster/posterBg' + posterBgIndex + '.jpg');
    this.posterBg = require('@/assets/poster/posterBg' + posterBgIndex + '.jpg');
    this.katongSrc = localStorage.getItem('katongSrc')
    this.posterMessageSrc = require('@/assets/poster/posterMessage' + this.$route.query.chooseIndex + '.png');


    setTimeout(function(){
      _this.createPoster();
    },1000);
  },
  methods: {
    createPoster() {
      let _this = this;
      html2canvas(document.getElementById('app')).then(function (canvas) {
        c = document.getElementById('app').appendChild(canvas);
        _this.indexShow = false;
      });
    },
  },
  // destroyed() {
  //   c && document.getElementById('app').removeChild(c);
  // }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.poster {
  width: 100%;
  height: 100vh;
}
.wishBox{
  width: 100%;
  height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
  position: relative;
}
.posterBg{
  width: 100%;
  height: 100%;
  display: block;
}
.posterIcon{
  width:66px;
  position: absolute;
  left: 50%;
  top: 15px;
  margin-left: -33px;
}
.posterMessageBox{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.personModel{
  width: 180px;
  display: block;
  position: absolute;
  right: -30px;
  top: -250px;
  z-index: 1;
}
.posterMessage{
  width: 100%;
  height: 220px;
  display: block;
  position: relative;
  z-index: 2;
}
.numBox{
  position: absolute;
  top: -50px;
  right: 10px;
  z-index: 3;
  width: 210px;
  height: 90px;
  background-color: #bb2d28;
  font-weight: 600;
  color: #fff;
  display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
  box-sizing: border-box;
  padding: 0 15px;
  font-size: 18px;
  border-radius: 20px;
}
.numBox:after{
  content:' ';
  position: absolute;
  right: 30px;
  top: -28px;
  width: 0;
  height: 0;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-bottom: 30px solid #bb2d28;
}
.tips{
  font-weight: 600;
  color: #bb2d28;
  font-size: 12px;
  position: fixed;
  bottom: 30px;
  left: 0;
  width: 100%;
  text-align: center;
  z-index: 1001;
}
.num{
  font-size: 30px;
}
.createPoster{
  width: 100px;
  height: 35px;
  background-color: #fff;
  border: 1px solid #e9e9e9;
  font-size: 14px;
  display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
</style>



