<template>
  <div class="hello">
    <div v-show="progress < 100">
      <div><img src="@/assets/logo.png" class="logo" /></div>
      <div class="loading-ic"><img src="@/assets/loading.gif" class="loading" /></div>
      <div class="progress">
      </div>
      <div><img src="@/assets/main-text.png" class="main-text"></div>
      <div><img src="@/assets/city.png" class="city"></div>
      <div><img src="@/assets/main-bottom-text.png" class="main-bottom-text"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      progress: 0
    }
  },
  created() {
    let me = this
    let timer = setInterval(() => {
      this.progress += 10
      if (this.progress === 100) {
        clearInterval(timer)
        timer = null
        me.$router.push({
          path: 'main'
        })
      }
    }, 200);
    
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  height: 100vh;
  background-color: #F4E6CD;
}
.logo{ 
  margin-top: 150px;
  width: 102px; 
  height: 76px;
}
.loading {
    width: 175px;
    height: 52px;
  margin-top: 20px;
}
.loading-ic {
    width: 175px;
    height: 52px;
    margin: 0 auto;
    position: relative
}
.main-text {
  margin-top: 44px;
  width: 319px;
  height: 35px;
}
.city-container {
  display: flex;
}
.city {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
}
.loading-bar {
  position: absolute;
}
.main-bottom-text {
  width: 232px;
  height: 14px;
  position: fixed;
  bottom: 8px;
  left: calc(50vw - 116px);
}
</style>



