import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import poster from '@/components/poster'
import chooseWish from '@/components/chooseWish'
import chooseWishPrePage from '@/components/chooseWishPrePage'
import chooseWishModel from '@/components/chooseWishModel'
import uploadAvatar from '@/components/uploadAvatar'
import main from '@/components/main'
import introduce from '@/components/introduce'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/poster',
      name: 'poster',
      component: poster
    },
    {
      path: '/chooseWish',
      name: 'chooseWish',
      component: chooseWish
    },
    {
      path: '/chooseWishModel',
      name: 'chooseWishModel',
      component: chooseWishModel
    },
    {
      path: '/chooseWishPrePage',
      name: 'chooseWishPrePage',
      component: chooseWishPrePage
    },
    {
      path: '/uploadAvatar',
      name: 'uploadAvatar',
      component: uploadAvatar
    },
    {
      path: '/main',
      name: 'main',
      component: main
    },
    {
      path: '/introduce',
      name: 'introduce',
      component: introduce
    }
  ]
})



