<template>
  <div class="introduce-container" :style="getStyle">
      <div class="introduce-talk">
        <img :src="getTalk()" class="talk-img" @click="wish"/>
      </div>
      <div class="wish-container1">
          <img src="@/assets/wish.png" class="wish-img" @click="wish"/>
          <img src="@/assets/change-place.png"  class="wish-img" @click="jump" />
      </div>
  </div>
</template>

<script>
import {placeConfig} from '@/constance/placeConfig'
export default {
  name: "chooseWishPrePage",
  data() {
    return {
      placeConfig
    };
  },
  mounted() {
    
  },
  methods: {
    getTalk() {
        const index = this.$route.query.index -0 || 0
        return placeConfig[index].text
    },
    jump() {
      this.$router.push({
          path: 'introduce',
          params: {
            currentPage:  this.$route.query.index || 0
          }
      })
    },
    wish() {
        this.$router.push({
            path: 'uploadAvatar',
            query: {
                index: this.$route.query.index || 0
            }
        })
    }
  },
  computed: {
      getStyle() {
          const index = this.$route.query.index || 0
          return {
            'background': `url(${this.placeConfig[index].image}) no-repeat`,
            'background-size': '100% 100vh'
          }
      }
  }
};
</script>

<style>
.introduce-container {
position: relative;
  width: 100%;
  height: 100vh;
  background-color: #f7f8f9;
 
}
.wish-container1 {
    position: fixed;
    bottom: 31px;
    width: 100vw;
    padding: 0 14px;
    display: flex;
    justify-content: space-between;
    left: 0;
}
.talk-img {
  width: 75%;
  height: 75%;
}
.wish-img {
    width: 125px;
    height: 32px;
}
.introduce-talk {
  position: fixed;
  bottom: 160px;
  right: 11px;
  animation:myfirst 2s;
  animation-fill-mode: forwards
}
@keyframes myfirst
{
	0%   {right:30px;}
	100% {right:0px;}
}
</style>



