<template>
  <div class="chooseWish">
    <div class="wishBox">
       <div class="choose-title">请选择你的告白语</div>
        <div class="wishitem" 
          :class="chooseIndex == index ? 'wishItemActive' : ''"
           @click="chooseWish(index)"
          v-for="(wishItem,index) in wishForm" 
          :key="index">
          <div class="wishitemRight" >{{wishItem.title}}</div>
        </div>
    </div>
    <div class="modelButton" @click="next">
      <img src="@/assets/upload/nextStep.png"/>
    </div>
  </div>
</template>

<script>
export default {
  name: "chooseWish",
  data() {
    return {
      indexShow:true,
      chooseIndex: 0,
      wishForm:[
          {
            title: '百年恰是风华正茂！愿伟大的党永葆生机活力，引领中华巨轮乘风破浪、逐梦前行。'
          },
          {
            title: '百年大党，百年辉煌。重温党史致敬百年，伟大复兴逐梦前行。'
          },
          {
            title: '铭记光辉历史，传承红色基因。爱党、爱国、爱人民，是我终身不变的追求。'
          },
          {
            title: '精准扶贫奔小康，民族复兴创辉煌。愿我们的党青春永驻，愿我们的祖国繁荣昌盛！'
          },
          {
            title: '不忘初心，牢记使命。我骄傲，我是中国共产党员；我承诺，对党忠诚，积极工作！'
          },
          {
            title: '我骄傲，我是中国人，我愿永远听党话、跟党走！'
          },
           {
            title: '东方风来满眼春，潮起正是扬帆时。壮怀激烈，慨当以歌，我们的征途是星辰大海，我们的前程是伟大复兴！'
          },
          {
            title: '牢记使命跟党走，改革开放再出发。'
          },
          {
            title: '作为深圳老一代拓荒牛，我有幸见证党和人民一手缔造的深圳实现了“五个历史性跨越”。我骄傲，我自豪！祝伟大的党生日快乐！'
          },
          {
            title: '新时代走在前列，新征程勇当尖兵。我们要坚定不移拥护党，感恩奋进，为深圳续写更多“春天的故事”出一份力。'
          },
          {
            title: '来了就是深圳人，党建引领聚英才。祝愿党的伟大事业人才济济，社会日新月异，国家繁荣富强！'
          },
          {
            title: '幸福是奋斗出来的，我们要弘扬“三牛”精神，激扬“四气”，争当“四者”，积极投身粤港澳大湾区和深圳先行示范区建设。'
          }
      ]
    };
  },
  mounted(){
    
  },
  methods: {
    chooseWish(index) {
      this.chooseIndex = index
    },
    next() {
      this.$router.push({
        path: 'poster',
        query:{
          chooseIndex:Number(this.chooseIndex) + 1
        }
      })
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.chooseWish{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    background: url('../assets/poster/uploadBg.png') no-repeat;
}
.wishBox{
    width: calc(100vw - 50px);
    height: calc(100vh - 120px);
    overflow: scroll;
    text-align: left;
    border-radius: 5px;
    padding: 25px;
    background-color: white;
}
.choose-title {
  display: flex;
  justify-content: center;
  font-size: 20px;
  font-family: zihun97hao-jinghongshoushu;
  font-weight: bold;
  color: #FF505D;
  line-height: 40px;
  margin-bottom: 20px;
}
.wishitem{
  width: 100%;
  box-sizing: border-box;
  background-color: #F1F1F1;
  border-radius: 3px;
  margin-bottom: 15px;
  padding: 10px 15px;
}
.wishItemActive{
  color: #fff; 
  background-color: #FFA19D;
  border: 1px solid #E2261E;
}
.wishitemRight{
  font-size: 10px;
}
.wishButton{
    margin: 0 auto;
    width: 80px;
    height: 32px;
    box-sizing: border-box;
    border-radius: 50px;
    color: white;
    background-color: red;
    font-size: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.modelButton {
  width: 100px;
  height: 35px;
  font-size: 14px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  margin-top: 20px;
  /* position: fixed;
  left: calc(50% - 50px);
  bottom: 50px; */
}
</style>



