<template>
  <div class="introduce-container">
    <div class="introduce-title">
      <img src="@/assets/click-ic.png" />
    </div>
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(item, index) in swiperImageConf" :key="index" @click="choose(index)">
          <img :src="item.image" class="slide-img" />
          <div class="slide-location-container">
             <img :src="item.loca" class="slide-place-img" />
          </div>
        </div>

      </div>
    </div>
    <div class="hand-container">
      <img :src="require('@/assets/hand-ic.png')" />
    </div>
  </div>
</template>

<script>
import {swiperImageConf} from '@/constance/swiperImageConf'
import Swiper from 'swiper'
import '@/package/swiper-bundle.min.css'
export default {
  name: "poster",
  data() {
    return {
      swiperImageConf,
      currentPage: swiperImageConf[0]
    };
  },
  mounted() {
    const me = this;
    const mySwiper = new Swiper ('.swiper-container', {
      on: {
        slideChange: function(swiper) {
          me.$set(me, "currentPage", me.swiperImageConf[swiper.activeIndex])
        }
      }
    })
  },
  methods: {
    choose(index) {
      localStorage.setItem('addressIndex',index);
      this.$router.push({
          path: 'uploadAvatar'
      })
    }
  },
};
</script>

<style>
.introduce-container {
  width: 100%;
  padding: 30px 20px 30px;
  background: url("../assets/choose-wish-bg.png") no-repeat;
  background-size: cover;
  height: 100vh;
  background-size:100% 100vh;
}
.introduce-title {
  padding-top: 60px;
  width: 280px;
  height: 38px;
  margin: 0 auto;
  padding-bottom: 70px;
}
.introduceBox {
  padding: 30px 20px 100px;
  height: 200px;
}
.date {
  font-size: 20px;
  font-weight: bold;
}
.wishBox{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.swiper-container {
  width: 279px;
  height: 333px;
}
.wish-container {
  width: 100px;
  height: 50px;
  border: 2px solid red;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}
.hand-container {
  position: fixed;
  bottom: 0;
  width: 81px;
  height: 106px;
  left: calc((100vw - 80px)/2);
}
.slide-location-container {
  padding-top: 36px;
  width: 260px;
  height: 30px;
  margin: 0 auto;
}
.swiper-slide {
  width: 279px;
  height: 333px;
}
.slide-img {
  width: 279px;
  height: 271px;
}
.img-container {
  margin: 30px 0px;
}
.people-container {
  width: 100vw;
  height: 400px;
  padding: 20px;
  position: relative;
}
.people-talk {
  border: 1px solid;
  width: 200px;
  height: 100px;
  padding: 20px;
  margin-left: 50px;
  z-index: 1;
  border-radius: 30px;
}
.people-img {
  position: absolute;
  width: 100px;
  height: 300px;
  right: 30px;
  top: 20px;
}
.swiper-wrapper {
  margin-top: 0;
}
</style>



