<template>
  <div class="pageBox">
    <div class="loadingBox" v-if="loadingShow">
      <img src="@/assets/loadingPage.gif">
      <div class="mt15">正在生成...</div>
    </div>
    <div class="uploadAvatarBox">
      <label class="addIconBox">
        <img src="@/assets/upload/cameraIcon.png" v-if="!katongSrc">
        <div v-if="katongSrc" class="katongSrcBox">
          <img class="katongSrc" :src="katongSrc"/>
        </div>
        <input
          type="file"
          ref="upload"
          style="display: none"
          @change="uploadFiles"
        />
      </label> 
      <img src="@/assets/upload/uploadTips.png" class="uploadTips"/>
      
      <div class="genderBox">
        <div class="genderBoxItem" 
          v-for="(genderItem, index) in genderForm"
          :key="index">
          <div class="genderBoxItemContainer">
            <div class="sexBox">
              <img :src="genderItem.sexBox" class="sexBoxBg" />
              <div class="genderBoxItemContentBox">
                <img :src="genderItem.sexAvatar" />
              </div>
            </div>
          </div>
          <div class="sexIcon" :class="genderItemActiveIndex === index ? 'sexIconActive' : ''" @click="genderItemActiveIndex = index;">
            <img :src="genderItem.sexIcon"/>
          </div>
        </div>
        
      </div>
      <div class="modelButton" @click="next">
        <img src="@/assets/upload/nextStep.png"/>
      </div>
    </div>
  </div>
</template>

<script>
var qs = require("querystring");
export default {
  name: "uploadAvatar",
  data() {
    return {
      genderForm: [
        {
          sexBox:require('@/assets/upload/sexBox1.png'),
          sexAvatar:require('@/assets/upload/sexAvatar1.png'),
          sexIcon:require('@/assets/upload/sexIcon1.png'),
        },
        {
          sexBox:require('@/assets/upload/sexBox2.png'),
          sexAvatar:require('@/assets/upload/sexAvatar2.png'),
          sexIcon:require('@/assets/upload/sexIcon2.png'),
        },
      ],
      genderItemActiveIndex: '',
      access_token: "",
      access_token1:'',
      access_token2:'',
      katongSrc: "",
      mergeSrc:'',
      loadingShow:false
    };
  },
  mounted() {
    this.getToken();
    this.getMergeToken();
    this.getBody_segToken();
  },
  methods: {
    getBody_segToken(){
      this.$get("https://aip.baidubce.com/oauth/2.0/token", {
        grant_type: "client_credentials",
        client_id: "ijiZp1PNrrSZz4lWmTc01jUC",
        client_secret: "ZGOisTIOsbzInLKfdVNTH5YeKYZBcBhx",
      })
        .then((res) => {
          this.access_token2 = res.data.access_token;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getMergeToken() {
      this.$get("https://aip.baidubce.com/oauth/2.0/token", {
        grant_type: "client_credentials",
        client_id: "q6y159UX6pe5cQlo6c6c52Hp",
        client_secret: "PHsyYYepgGEu5anL8NgUoMX1Q82lFkFl",
      })
        .then((res) => {
          this.access_token1 = res.data.access_token;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getToken() {
      this.$get("https://aip.baidubce.com/oauth/2.0/token", {
        grant_type: "client_credentials",
        client_id: "u5uHwm5ejuiOKCHP0MhTNbvl",
        client_secret: "gyFnGUXWErWBRWkkWZwwOqxMN0u7qGBR",
      })
        .then((res) => {
          this.access_token = res.data.access_token;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 百度头像卡通化
    uploadFiles() {
      const _this = this;
      let file = this.$refs.upload; //获取文件数据
      let fileList = file.files;
      let fileSize = fileList[0].size;
      let fileType = fileList[0].type.split('/')[0];
      var imgFile;
      let reader = new FileReader(); //html5读文件

      if(this.genderItemActiveIndex === ''){
        alert('请你先选择性别');
        return
      }


      reader.readAsDataURL(fileList[0]); //转BASE64
      reader.onload = function (e) {
        var image = new Image();
        image.src = e.target.result;
        image.onload = function() {
          if(this.width > 1920){
            alert('该图片尺寸过大，请重新上传')
            return
          }
          if(this.height > 1080){
            alert('该图片尺寸过大，请重新上传')
            return
          }
        };

        if(fileType.indexOf('image') == -1){
          alert('支持图片文件上传')
          return
        }
        if(fileSize/1024/1024 > 5){
          alert('文件太大了，请压缩后再上传')
          return
        }

        //读取完毕后调用接口
        imgFile = e.target.result;
        imgFile = imgFile.split("base64,")[1];

        let formData = new FormData();
        formData.append("image", imgFile);
        formData.append("type", 'anime');

        _this.postImageMerge(imgFile,'data:image/png;base64,');

        // return _this.$post(
        //   'https://aip.baidubce.com/rest/2.0/image-process/v1/selfie_anime?access_token=' + _this.access_token,formData)
        // .then((res) => {
        //   _this.postImageMerge(res.data.image,'data:image/png;base64,');
        //   // _this.katongSrc = e.target.result.split("base64,")[0] + 'base64,' + res.data.image;
        // })
        // .catch((err) => {
        //   console.log(err);
        // });
      };
    },
    // // 百度图像融合
    postImageMerge(toTarget,startString){
      let sourceImage = '';

      if(this.genderItemActiveIndex != 1){
        sourceImage = localStorage.getItem('model1');
      }else{
        sourceImage = localStorage.getItem('model2');
      }
      
      let params = {
        version:'3.0',
        image_template:{
          image:sourceImage,
          image_type:'BASE64',
        },
        image_target:{
          image:toTarget,
          image_type:'BASE64',
        }
      };

      this.$postMerge('https://aip.baidubce.com/rest/2.0/face/v1/merge?access_token=' + this.access_token1,params).then((res) => {
        console.log(res);
        this.mixedByBaidu(res.data.result.merge_image,startString);
        // this.katongSrc = startString + res.data.result.merge_image;
        // localStorage.setItem('katongSrc',JSON.stringify(this.katongSrc));
      })
      .catch((err) => {
        console.log(err);
      });
    },
    // 百度人像分割
    mixedByBaidu(toTarget,startString){
      let formData = new FormData();
      formData.append("image", toTarget);
      formData.append("type", 'foreground');

      this.$post('https://aip.baidubce.com/rest/2.0/image-classify/v1/body_seg?access_token=' + this.access_token2,formData).then((res) => {
          console.log(res);
          this.katongSrc = startString +  res.data.foreground;
          this.loadingShow = false;
        // this.katongSrc = startString + res.data.result.merge_image;
        localStorage.setItem('katongSrc',this.katongSrc);
      })
      .catch((err) => {
        console.log(err);
      });
    },
    next() {
      // if(this.genderItemActiveIndex === ''){
      //   alert('请先选择性别');
      //   return
      // }

      if(this.katongSrc === ''){
        alert('请先上传头像');
        return
      }

      this.$router.push({
        path: "chooseWish",
      });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pageBox{
  width: 100%;
  min-height: 100vh;
  background: url('../assets/poster/uploadBg.png') no-repeat;
  background-size: cover;
  padding-top: 30px;
}
.uploadAvatarBox {
  width: calc(100% - 40px);
  height: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  background-color: #fff;
  border-radius: 6px;
  box-sizing: border-box;
  padding: 20px;
}
.uploadTips {
  width: 250px;
  display: block;
  margin: 25px 0;
}
.addIconBox {
  width: 120px;
  height: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.addIconBox img{
  width: 120px;
  height: 120px;
  display: block;
}
.katongSrcBox{
  width: 120px;
  height: 120px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.katongSrc {
  max-width: 120px;
  max-height: 120px;
  display: block;
}
.genderBox {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
}
.genderBoxItem{
  width: calc(50% - 10px);
}
.genderBoxItemContainer {
  width: 100%;
  position: relative;
}
.sexBoxBg{
  width: 100%;
  display: block;
}
.genderBoxItemContentBox{
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
}
.genderBoxItemContentBox img{
  width: 90px;
  height: 90px;
  display: block;
}
.sexIcon{
  width: 100%;
  margin-top: 20px;
}
.sexIconActive{
  width: 100%;
  margin-top: 20px;
  position: relative;
}
.sexIconActive:after{
  content:' ';
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  background-color: rgba(0,0,0,.2);
  width: 100%;
  height: 100%;
  border-radius: 23px;
}
.modelButton {
  width: 100px;
  height: 35px;
  font-size: 14px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  position: fixed;
  left: calc(50% - 50px);
  bottom: 50px;
}
.loadingBox{
  width: 100%;
  height: 100vh;
  background-color: rgba(0,0,0,.8);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1001;
  font-size: 16px;
  color: #fff;
}
.loadingBox img{
  width: 30px;
  display: block;
}
</style>



